import { useTranslation } from "react-i18next";
import style from "./CustomRadioButton.module.css";
const CustomRadioButton = ({
  label,
  options,
  onChange,
  defaultValue,
  customFormInputContainer,
  customFormInputLabel,
  errorShow,
  msg,
  disabled,
  customFormInput,
  children,
}) => {
  const { t } = useTranslation();
  return (
    <>
      <div
        className={style.form_input_container}
        style={customFormInputContainer}
      >
        <label className={style.form_input_label} style={customFormInputLabel}>
          {label}
          {msg ? (
            <>
              <span
                style={{
                  marginLeft: "15px",
                  color: "red",
                }}
              >
                {" "}
                {` `} {msg}
              </span>
            </>
          ) : errorShow ? (
            <span
              style={{
                marginLeft: "15px",
                color: "red",
              }}
            >
              * {t("form_require_label")}
            </span>
          ) : null}
          {children}
        </label>
        {options?.map((ch) => (
          <label className={style.crb} key={ch}>
            <span className={style.form_input} style={customFormInput}>
              {ch}
            </span>
            <input
              type="radio"
              value={ch}
              checked={defaultValue === ch ? true : false}
              onChange={onChange}
              disabled={disabled}
            />
            <span className={style.radiomark}></span>
          </label>
        ))}
      </div>
    </>
  );
};

export default CustomRadioButton;
