import { useTranslation } from "react-i18next";
import style from "./Button.module.css";
const FormCreateButton = ({ label, count, customStyle, isTranslate }) => {
  const { t } = useTranslation();

  return isTranslate ? (
    <button
      className={style.create_btn_container}
      style={customStyle}
      type="submit"
    >
      {count > 0 ? <span>{count} </span> : null}
      {label}
    </button>
  ) : (
    <button
      className={style.create_btn_container}
      style={customStyle}
      type="submit"
    >
      {count > 0 ? <span>{count} </span> : null}
      {t(label)}
    </button>
  );
};

export default FormCreateButton;
