import { useTranslation } from "react-i18next";
import style from "./CustomCheckbox.module.css";
const CustomCheckbox = ({
  label,
  options,
  onChange,
  defaultValue,
  customFormInputContainer,
  customFormInputLabel,
  errorShow,
  msg,
  disabled,
  isCheck,
  isOptionError,
  customFormInput,
  children,
}) => {
  const { t } = useTranslation();
  return (
    <>
      <div
        className={style.form_input_container}
        style={customFormInputContainer}
      >
        <label className={style.form_input_label} style={customFormInputLabel}>
          {label}
          {!isOptionError && msg ? (
            <>
              <span className={style.form_input_label_error}>
                {" "}
                {` `} {msg}
              </span>
            </>
          ) : !isOptionError && errorShow ? (
            <span className={style.form_input_label_error}>
              * {t("form_require_label")}
            </span>
          ) : null}
          {children}
        </label>
        {options?.map((ch) => (
          <label className={style.ccc} key={ch}>
            <span className={style.form_input} style={customFormInput}>
              {ch}
              {isOptionError && errorShow ? (
                <span
                  style={{
                    color: "red",
                    marginLeft: "15px",
                    fontSize: "13px",
                  }}
                >
                  * {t("form_require_label")}
                </span>
              ) : null}
            </span>
            {isCheck ? (
              <input
                type="checkbox"
                checked={isCheck ? defaultValue : false}
                onChange={onChange}
                disabled={disabled}
              />
            ) : (
              <input
                type="checkbox"
                checked={defaultValue?.includes(ch) ? true : false}
                onChange={onChange}
                disabled={disabled}
              />
            )}

            <span className={style.checkmark}></span>
          </label>
        ))}
      </div>
    </>
  );
};

export default CustomCheckbox;
